(function() {
  'use strict';

  angular
    .module('detroitstartupjobs', ['ngAnimate', 'ngCookies', 'ngTouch', 'ngSanitize', 'ngMessages', 'ngAria', 'ngResource', 'ui.router', 'ngMaterial', 'toastr']);

})();

(function() {
  'use strict';

  angular
    .module('detroitstartupjobs')
    .controller('HomeController', HomeController);

  /** @ngInject */
  function HomeController () {

    var vm = this;

    vm.jobs = [
      {
        "title": "Campus Director",
        "created_on": "Friday",
        "salary": 88000,
        "employer": "The Iron Yard",
        "location": "Detroit",
        "skills": ["JavaScript", "iOS", "Android", "Adobe Illustrator"]
      },
      {
        "title": "Post Production Coordinator",
        "created_on": "Wed",
        "salary": 30000,
        "employer": "Heather Read Photography & Design",
        "location": "Royal Oak",
        "skills": ["Adobe Photoshop"]
      }
    ]
  }

})();

(function() {
  'use strict';

  angular
    .module('detroitstartupjobs')
    .run(runBlock);

  /** @ngInject */
  function runBlock($log) {

    $log.debug('runBlock end');
  }

})();

(function() {
  'use strict';

  angular
    .module('detroitstartupjobs')
    .config(routerConfig);

  /** @ngInject */
  function routerConfig($stateProvider, $urlRouterProvider) {
    $stateProvider
      .state('home', {
        url: '/',
        templateUrl: 'app/home/home.html',
        controller: 'HomeController',
        controllerAs: 'home'
      });

    $urlRouterProvider.otherwise('/');
  }

})();

/* global malarkey:false, moment:false */
(function() {
  'use strict';

  angular
    .module('detroitstartupjobs')
    .constant('malarkey', malarkey)
    .constant('moment', moment);

})();

(function() {
  'use strict';

  angular
    .module('detroitstartupjobs')
    .config(config);

  /** @ngInject */
  function config($logProvider, toastrConfig) {
    // Enable log
    $logProvider.debugEnabled(true);

    // Set options third-party lib
    toastrConfig.allowHtml = true;
    toastrConfig.timeOut = 3000;
    toastrConfig.positionClass = 'toast-top-right';
    toastrConfig.preventDuplicates = true;
    toastrConfig.progressBar = true;
  }

})();

angular.module("detroitstartupjobs").run(["$templateCache", function($templateCache) {$templateCache.put("app/home/home.html","<md-toolbar><div class=\"md-toolbar-tools\"><md-button class=\"md-icon-button\" aria-label=\"Settings\"><md-icon md-font-library=\"material-icons\">menu</md-icon></md-button><h2><span>Detroit Startup Jobs</span></h2><span flex=\"\"></span><md-button class=\"md-icon-button\" aria-label=\"Account\"><md-icon md-font-library=\"material-icons\">face</md-icon></md-button></div></md-toolbar><md-card layout=\"row\"><div flex=\"\" flex-offset=\"33\"><md-list><md-list-item ng-repeat=\"job in home.jobs\">{{ job.title }}</md-list-item></md-list></div></md-card>");}]);